body {
  margin: 0;
  font-family: Syne, Inter, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  margin: 0;
  padding: 0;
  background: #151414;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

/*just to use*/
.pointer {
  cursor: pointer;
}



.single-line {
  display: flex;
  flex-direction: row;
}

.single-line-center-between {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.single-line-baseline-start {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: flex-start;
}

.single-line-center-start {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

.single-line-center-end {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
}

/*container*/
.custom-container {
  width: 100%;
  max-width: 1200px;
  margin: auto;
}

@media (max-width: 600px) {
  .custom-container {
    padding: 0 24px;
  }
}

/*materials*/
.custom-mahadao-material1 {
  background: linear-gradient(180deg, #48423E 0%, #373030 100%);
  box-shadow: 0px 12px 24px -4px rgba(0, 0, 0, 0.12), 0px 16px 20px rgba(0, 0, 0, 0.25);
  border-radius: 6px;
  height: 100%;
  width: 100%;
}

.custom-mahadao-material2 {
  position:relative;
  background: rgba(255, 255, 255, 0.02);
  backdrop-filter: blur(21px);
  border-radius: 6px;
  padding: 32px;
  height: 100%;
}

.custom-mahadao-material2::before {
  content:"";
  position:absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
  border-radius: 12px;
  padding:1px;
  background: linear-gradient(180deg, rgba(255, 116, 38, 0.1) 0%, rgba(255, 255, 255, 0) 100%);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: destination-out;
  mask-composite: exclude;
  z-index: -1;
}

@media (max-width: 600px){
  .custom-mahadao-material2 {
    padding: 32px 24px;
  }
}

.custom-mahadao-material3 {
  background: linear-gradient(38.44deg, #F47F57 23.51%, #FD5656 69.95%);
  border-radius: 6px;
}

