}

.marginB40 {
  margin-bottom: 40px;
}

.marginB32 {
  margin-bottom: 32px;
}

.marginB20 {
  margin-bottom: 20px;
}

.marginB16 {
  margin-bottom: 16px;
}

.marginB08 {
  margin-bottom: 8px;
}

.marginB04 {
  margin-bottom: 4px;
}

.marginR12 {
  margin-right: 12px;
}

