.paddingT40 {
  padding-top: 40px;
}

.page-height {
  min-height: calc(100vh - 80px);
}

.input-error {
  border: 1px solid #FA4C69;
}

.input-warning {
  border: 1px solid #F09700;
}

.input-font-error {
  font-family: Inter;
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 130%;
  color: #FA4C69;
  margin-top: 4px;
  margin-bottom: 0;
}

.input-font-warning {
  font-family: Inter;
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 130%;
  color: #FCB400;
  margin-top: 4px;
  margin-bottom: 0;
}
